.main-index {
  margin: 0;
}

.main-index__hero {
  position: relative;
  width: 320px;
  margin: 0;
  padding: 0;
  background: url("../img/hero/cat-background.png");
  background-size: 320px 320px;
  background-position: left top;
  background-repeat: no-repeat;
  box-sizing: border-box;
  
  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 320px;
    height: 320px;
    background: @transparent-green;
    z-index: 1;
  }

  @media (min-width: @tablet-width) {
    width: 768px;
    background: @White;
    outline: 1px solid #bb3fb1;
  }

  @media (min-width: @desktop-width) {
    width: 1440px;
    background: @White;
    outline: 1px solid #bb3fb1;
  }
}
