.hero {
  position: relative;
  margin: 0;
  padding: 27px 20px 20px 20px;
  z-index: 50;
  outline: 1px solid #bb3fb1;

  @media (min-width: @tablet-width) {
    padding: 27px 20px 20px 108px;
  }
}

.hero__content {
  margin: 0;
  text-align: center;
  outline: 1px solid #bb3fb1;

  @media (min-width: @tablet-width) {
    width: 414px;
    margin: 0;
    text-align: left;
  }
}

.hero__title {
  margin: 0 0 25px 0;
}

.title-hero {
  position: relative;
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
  // text-align: center;
  color: @White;
  z-index: 5;

  @media (min-width: @tablet-width) {
    font-size: 50px;
    line-height: 60px;
    color: @black;
  }
}

.hero__subtitle {
  margin: 0 0 29px 0;
}

.hero__button {
  color: @White;
  background-color: @basic-green;
}

.hero__image {
  position: relative;
  z-index: 5;
}
